* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

/* ------------------------------- NAVBAR ------------------------------- */

.navbar {
  font-family: "Roboto Slab", serif;
  font-size: 18px;
  background-color: black;
  border: none;
  padding-bottom: 10px;
  width: 100%;
  position: fixed;
  z-index: 5;
}

.main-nav {
  list-style-type: none;
  display: none;
}

.nav-links,
.logo {
  text-decoration: none;
  color: rgba(255, 255, 255, 1);
}

.main-nav li {
  text-align: center;
  margin: 15px auto;
}

.logo {
  display: inline-block;
  font-size: 22px;
  margin-top: 10px;
  margin-left: 20px;
}

.navbar-toggle {
  position: absolute;
  top: 10px;
  right: 20px;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.8);
  font-size: 24px;
}

.active {
  display: block;
}

@media screen and (min-width: 768px) {
  .navbar {
    display: flex;
    justify-content: space-between;
    padding-bottom: 0;
    height: 50px;
    align-items: center;
  }

  .main-nav {
    display: flex;
    margin-right: 30px;
    flex-direction: row;
    justify-content: flex-end;
  }

  .main-nav li {
    margin: 0;
  }

  .nav-links {
    margin: 0 15px 0 15px;
  }

  .logo {
    margin-top: 0;
  }

  .navbar-toggle {
    display: none;
  }

  .logo:hover,
  .nav-links:hover {
    color: rgba(255, 255, 255, 0.7);
  }
}

.wrapper {
  width: 100%;
  margin: 0 50px 0 0;
  padding: 0;
}

.container {
  width: 100%;
  padding: 50px 0;
}

/* ------------------------------- ANIMATIONS ------------------------------- */

@keyframes fadeIn {
  0% {
    transform: translateY(-10%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes wiggle {
  0% {
    transform: rotate(5deg);
  }
  25% {
    transform: rotate(-5deg);
  }
  50% {
    transform: rotate(10deg);
  }
  75% {
    transform: rotate(-2.5deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes widen {
  0% {
    transform: scale3d(1, 1, 1);
    opacity: 0.2;
  }
  50% {
    transform: scale3d(1.1, 1.1, 1.1);
    opacity: 1;
  }
  100% {
    transform: scale3d(1, 1, 1);
    opacity: 0.2;
  }
}

.fall {
  animation: fadeIn 2s;
}

.widen:hover {
  animation: widen 1.3s ease-in-out infinite;
}

.grow {
  transition: all 0.2s ease-in-out;
}

.grow:hover {
  transform: scale(1.1);
}

/* ------------------------------- BACKGROUND CONTROL ------------------------------- */

#intro {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("./assets/self-min.jpg") no-repeat center center fixed;
  background-size: cover;
  padding: 20% 0 20% 0;
  text-align: center;
  color: white;
  height: auto;
}

#about {
  background: white;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5% 0 10% 0;
  height: auto;
}

#skills {
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url("./assets/sf-min.jpg") no-repeat center center fixed;
  background-size: cover;
  height: auto;
}

#projects {
  background: #542437;
  height: auto;
}

#resume {
  background: linear-gradient(
      rgba(216, 211, 255, 0.7),
      rgba(241, 234, 250, 0.2)
    )
    no-repeat center center;
  height: 300px;
}

#contact {
  background: #333;
  height: auto;
}

@media screen and (max-width: 768px) {
  #intro {
    background: linear-gradient(
        rgba(26, 139, 204, 0.7),
        rgba(26, 139, 204, 0.2)
      )
      fixed;
    padding: 20% 0 20% 0;
  }

  #skills {
    background: linear-gradient(rgba(187, 58, 58, 0.7), rgba(233, 32, 32, 0.2))
      fixed;
  }
}

/* ------------------------------- INTRO ------------------------------- */

#intro .title {
  font-size: 90px;
  font-family: "Roboto Slab", serif;
}

#intro .desc {
  font-size: 25px;
  margin-top: 20px;
  font-family: "Source Sans Pro", sans-serif;
  font-style: italic;
  font-weight: bold;
}

#intro .name {
  background-color: #001f3f;
  margin: -20px;
  padding: 0 20px 0 20px;
  border-radius: 25px;
}

/* ------------------------------- ABOUT ------------------------------- */

#about .container {
  width: 75%;
  height: 95%;
  text-align: center;
}

#about h1 {
  font-family: "Julius Sans One", sans-serif;
  font-size: 40px;
  margin-bottom: 25px;
}

#about .image {
  display: block;
  height: 150px;
  width: 150px;
  border-radius: 50%;
  box-shadow: 2px 2px 5px lightgray;
  margin: auto;
  margin-bottom: 25px;
}

#about .desc {
  font-family: "Roboto Slab", sans-serif;
  display: inline-block;
  width: 75%;
  height: 60%;
  margin: 10px;
  border-top: 2px solid lightgray;
  border-bottom: 2px solid lightgray;
  padding: 20px;
  line-height: 25px;
}

#about .innerDesc {
  width: 100%;
  text-align: left;
  margin: auto;
  margin-bottom: 25px;
  padding: 0 15px 0 15px;
}
#about .em {
  color: rgba(200, 60, 60, 1);
  font-weight: bold;
}

#about .iconBox {
  width: 60%;
  background: #ddd;
  text-align: center;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
}

#about .icon {
  max-width: 33%;
}

@media screen and (max-width: 1000px) {
  #about .container {
    padding: 5px;
  }
  #about .desc {
    width: 80%;
    padding: 0 0 10px 0;
  }
}

@media screen and (max-width: 768px) {
  #about .container {
    width: 90%;
    padding: 5px;
  }
  #about .desc {
    width: 85%;
    padding: 0 0 10px 0;
  }
}

/* ------------------------------- SKILLS ------------------------------- */

#skills .container {
  margin: auto;
  width: 100%;
  height: 90%;
  text-align: center;
  padding: 20px;
}

#skills h1 {
  font-family: "Julius Sans One", sans-serif;
  font-weight: bold;
  font-size: 50px;
  margin: 25px 0 25px 0;
  text-shadow: 0 0 5px black;
  color: white;
}

#skills .innerContainer {
  margin: auto;
  width: 90%;
  height: 90%;
  background: rgba(150, 150, 150, 0.6);
  border-radius: 25px;
  box-shadow: 0 0 5px lightgray;
  padding: 20px;
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
}

#skills .logoContainer {
  flex: 1;
  padding: 20px;
  font-family: "Noto Sans", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  color: white;
}

#skills img {
  max-width: 130px;
}

@media screen and (max-width: 768px) {
  #skills .container {
    padding: 5px;
  }
  #skills .innerContainer {
    width: 100%;
    padding: 0;
  }
}

/* ------------------------------- PROJECTS ------------------------------- */

#projects .container {
  margin: -25px auto;
  width: 100%;
}

#projects h1 {
  text-align: center;
  font-family: "Julius Sans One", sans-serif;
  font-size: 50px;
  font-weight: bold;
  color: white;
  margin-bottom: 25px;
}

#projects h2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
}
#projects h4 {
  text-align: left;
  font-style: italic;
  margin-bottom: 10px;
}

#projects p {
  padding: 5px;
}

#projects .date {
  font-size: 15px;
}

#projects .projectsContainer {
  font-family: "Roboto Slab", sans-serif;
  margin: auto;
  width: 95%;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
}

#projects .project {
  height: auto;
  margin: 10px;
  padding: 10px;
  width: 33%;
  background: linear-gradient(
    rgba(200, 200, 200, 0.1),
    rgba(200, 200, 200, 0.1)
  );
  border-radius: 25px;
  box-shadow: 0 0 5px gray;
}

#projects .misc {
  font-family: "Roboto Slab", sans-serif;
  margin: auto;
  color: white;
  display: flex;
  justify-content: space-evenly;
}

#projects ul {
  list-style-type: none;
}

#projects img {
  width: 100%;
  object-fit: cover;
}

#projects a {
  text-align: center;
  color: aquamarine;
  padding: 5px;
  text-decoration: underline;
}

@media screen and (max-width: 905px) {
  #projects .projectsContainer {
    flex-direction: column;
  }

  #projects .project {
    width: 95%;
  }
}

/* ------------------------------- RESUME ------------------------------- */

#resume .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#resume h1 {
  margin-top: -15px;
  font-family: "Julius Sans One", sans-serif;
  font-weight: bold;
}

#resume .button {
  font-family: "Roboto Slab", sans-serif;
  font-weight: bold;
  width: 175px;
  padding: 25px;
  margin: 25px auto;
  border: 1px solid black;
  border-radius: 25px;
  text-align: center;
}
